<template>
  <div class="set">
    <bread></bread>
    <Tabs  small type="card" ref="tabs" v-model="tabIndex">
      <TabPane  label="开放平台设置">
        <open></open>
      </TabPane >
      <TabPane  label="网站信息设置">
        <web></web>
      </TabPane >
      <TabPane  label="网站支付设置">
        <pay></pay>
      </TabPane >
      <TabPane  label="信息通知设置">
        <message></message>
      </TabPane >
    </Tabs>
  </div>
</template>
<script>
  import bread from '../bread.vue'
  import open from './open.vue'
  import web from './web.vue'
  import pay from './pay.vue'
  import message from './message.vue'
export default{
  data(){
    return {
      tabIndex:0
    }
  },
  components:{bread,open,web,pay,message}
}
</script>
<style>
  .set{
    width: 98%;
    margin: 0 auto;
  }
  .card-header{
    background: none !important;
  }
   .small{
     width: 100% !important;
   }
   .nav-tabs .nav-link.active, .nav-tabs .nav-item.show .nav-link{
     background: none;
     border-bottom: 1px solid #F6F7FA;
     color: #6098FF;
   }
   .nav-tabs .nav-link {
     font-size: 15px;
    border-top-left-radius: 6px !important;
    border-top-right-radius: 6px !important;
}
.nav-link {
     padding: 0.5rem 1.5rem !important;
}
a{
  color: #000000;
}
</style>



